.NavBar {
    background: #154354;
    height: 40px;
    width: 100%;
    position: fixed;
    z-index: 999;
    opacity: 0.7;
    display: flex;
    justify-content: center;
    align-items: center;
}
.NavBar nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.NavBar nav ul li {
   display: inline-block;
   margin: 1.5vw;
}
.NavBar nav ul li a {
    text-decoration: none;
    color: #f7f7f7;
}
.NavBar a:hover {
    border-bottom: none;
    color: black;
}
.NavBar .MobileNav {
    display: none;
}

@media (max-width: 900px) {
    .NavBar .Navigation {
        display: none;
    }
   
    .NavBar .MobileNav {
        display: flex;
        align-items: center;

    }
    .NavBar .MobileNav .hamburger {
        position: absolute;
        right: 5%;
        cursor: pointer;
    }

    .NavBar nav ul {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 40px;
        right: 0;
        width: 70%;
        height: 80vh;
        background: #154354;
        border-top: 2px solid #9fa8ac;
        font-size: 25px;
        opacity: 0.9;
        text-align: right;
    }

    .NavBar nav ul li {
        padding-left: 15px;
    }
    
    .NavBar{
        opacity: 0.9;
    }
    .NavBar a:hover {
        border-bottom: none;
        color: black;
    }


}
